'use strict';

var linear = function (t) {
	return t;
};

/**
 * 
 * @public	 
 * @method animate
 * @returns {Object} controls
 */
var animate = function () {
	var percent = 0;
	var step = 0.01;
	var state = false;
	var watch;

	/**
	 *  `animate`
	 */
	var animate = function () {
		percent += step;
		if (percent <= 1 && state) {
			watch(+linear(percent));
			requestAnimationFrame(animate);
		} else {
			watch(1.0);
			state = false;
		}
	};

	/**
	 *  `play`
	 */
	var play = function () {
		if (state === true) {
			return;
		}
		state = true;
		animate();
	};

	/**
	 *  `pause`
	 */
	var pause = function () {
		state = false;
	};

	/**
	 *  `gotoAndPlay`
	 */
	var gotoAndPlay = function (x) {
		percent = x;
		play();
	};

	/**
	 *  `gotoAndStop`
	 */
	var gotoAndStop = function (x) {
		percent = x;
		pause();
	};

	/**
	 *  `controls`
	 */
	var controls = function () {
		return {
			play : play,
			pause : pause,
			gotoAndPlay : gotoAndPlay,
			gotoAndStop : gotoAndStop
		};
	};

	/**
	 *  `duration`
	 */
	controls.duration = function (x) {
		var count = (x || 500) * 60 / 1000;
		step = 1 / count;
		return controls;
	};

	/**
	 *  `watch`
	 */
	controls.watch = function (x) {
		watch = x;
		return controls;
	};

	return controls;
};

/**
 * Export `animate`
 */
module.exports = animate;